import React, { useRef } from 'react';
import zxcvbn from 'zxcvbn';
import { useNavigate } from 'react-router-dom';
// services
import toastService from '../../../services/toast/toastService';
// repository
import authRepository from '../../../repositories/authRepository';
// MUI Components
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import LoadingButton from '@mui/lab/LoadingButton';
import InputAdornment from "@mui/material/InputAdornment";
import IconButton from "@mui/material/IconButton";
import Divider from '@mui/material/Divider';
import Typography from '@mui/material/Typography';
import Link from '@mui/material/Link';
import Paper from '@mui/material/Paper';
import Button from '@mui/material/Button';
// Page components
import Branches from './fields/Branches';
import Birthdate from './fields/Birthdate';
import Gender from './fields/Gender';
import Nationality from './fields/Nationality';
import RegisterCover from '../../../../assets/register-cover.png';
import ContactNumber from './fields/ContactNumber';
import CivilStatus from './fields/CivilStatus';
// react-icons
import { FaArrowLeft, FaArrowRight, FaCheck, FaUserPlus, FaRegCheckCircle, FaSignInAlt } from 'react-icons/fa';
import { MdVisibility, MdVisibilityOff } from 'react-icons/md';


export default function NewMemberForm() {

    const navigate = useNavigate();
    const passwordRef = useRef();
    const repasswordRef = useRef();

    const [loading, setLoading] = React.useState(false);
    const [step, setStep] = React.useState(0);
    // Basic Profile  
    const [lastName, setLastName] = React.useState("");
    const [firstName, setFirstName] = React.useState("");
    const [middleName, setMiddleName] = React.useState("");
    const [nameSuffix, setNameSuffix] = React.useState("");
    const [gender, setGender] = React.useState("");
    const [birthdate, setBirthdate] = React.useState(null);
    const [civilStatus, setCivilStatus] = React.useState("");
    const [nationality, setNationality] = React.useState("Filipino");
    const [contactNumber, setContactNumber] = React.useState(null); 

    // Account
    const [branch, setBranch] = React.useState("");
    const [email, setEmail] = React.useState("");
    const [code, setCode] = React.useState("");
    // Password 
    const [password, setPassword] = React.useState("");
    const [repassword, setRePassword] = React.useState("");
    const [passwordMatch, setPasswordMatch] = React.useState(true);
    const [passwordStrength, setPasswordStrength] = React.useState("Too short");
    const [passwordColor, setPasswordColor] = React.useState("#E45570");
    const [isShowPassword, setIsShowPassword] = React.useState(false);
    const toggleIsShowPassword = () => setIsShowPassword(!isShowPassword);
    const [isShowRePassword, setIsShowRePassword] = React.useState(false);
    const toggleIsShowRePassword = () => setIsShowRePassword(!isShowRePassword);


    const handleStep = (step) => {
        setStep(step)
    }

    const handlePassword = (password) => {
        setPassword(password);
        checkMatchPassword();

        let score = zxcvbn(password).score;

        switch (score) {
            case 1: setPasswordStrength("Weak"); setPasswordColor("#E45570"); break;
            case 2: setPasswordStrength("Weak"); setPasswordColor("#E45570"); break;
            case 3: setPasswordStrength("Strong"); setPasswordColor("#00BFA5"); break;
            case 4: setPasswordStrength("Very strong"); setPasswordColor("#00BFA5"); break;
            default: setPasswordStrength("Too short"); setPasswordColor("#E45570"); break;
        }
    }

    const handleRetypePassword = (password) => {
        setRePassword(password);
        checkMatchPassword();
    }

    const checkMatchPassword = () => {
        let pass = passwordRef.current.value;
        let repass = repasswordRef.current.value;
        if (pass !== "") {
            setPasswordMatch(pass === repass);
        }
    }

    const handleSubmitEmail = async (e) => {
        try {
            e.preventDefault();

            if (password !== repassword) {
                toastService.modalError('Password doesn\'t match');
                return false;
            }

            let data = {
                email
            }

            setLoading(true);
            let result = await authRepository.sendCode(data);
            if (!result.error) {
                handleStep(1);
            } else {
                toastService.modalError(result.message);
            }

        } catch (error) {
            toastService.modalError(error.toString());
        }

        setLoading(false);
    }

    const handleCode = async (e) => {
        try {
            e.preventDefault();

            let data = {
                code,
                email
            }

            setLoading(true);
            let result = await authRepository.verify(data);
            if (!result.error) {
                handleStep(2);
            } else {
                toastService.modalError(result.message);
            }

        } catch (error) {
            toastService.modalError(error.toString());
        }

        setLoading(false);

    }

    const handleRegister = async (e) => {
        try {
            e.preventDefault();

            setLoading(true);

            let data = {
                branch,
                email,
                password,
                password_confirmation: repassword,
                lastName,
                firstName,
                middleName,
                nameSuffix,
                gender,
                civilStatus,
                birthdate,
                nationality,
                contactNumber, 
            }

            let result = await authRepository.register(data); 

            if (!result.error) {
                setStep(3);
                window.scrollTo(0,0);
            }else{
                toastService.modalError(result?.message);
            }

        } catch (error) {
            toastService.modalError("Invalid input");
        }

        setLoading(false);
    }

    return (
        <div style={{ width: '100%' }}>
            <Box sx={{ minHeight: 420 }}>
                <Grid container spacing={2}>
                    {step !== 3 &&
                        <React.Fragment>
                            <Grid item xs={12} md={7} className="d-xs-none">
                                <Paper elevation={0} sx={{
                                    backgroundColor: "transparent !important",
                                    backgroundImage: `url(${RegisterCover})`,
                                    backgroundSize: "500px 400px",
                                    backgroundPosition: "center",
                                    backgroundRepeat: "no-repeat",
                                    width: 600,
                                    height: 400
                                }}>
                                </Paper>
                            </Grid>
                            <Grid item xs={12} md={5}>
                                <Box sx={{ mb: 2, pl: 1, textAlign: 'center' }}>
                                    <Typography variant="h5" color="primary"><FaUserPlus /> Create New Account</Typography>
                                </Box>
                                <Divider />
                                {step === 0 &&
                                    <form onSubmit={(e) => handleSubmitEmail(e)} autoComplete="off">
                                        <Box sx={{ mx: 'auto', width: '100%', mt: 2 }}>
                                            <Box sx={{ m: 1, mr: -1, p: 0 }}>
                                                <Branches setBranch={setBranch} branch={branch} />
                                            </Box>
                                            <TextField
                                                sx={{ m: 1 }}
                                                onChange={(e) => setEmail(e.target.value)}
                                                value={email}
                                                variant="outlined"
                                                size="large"
                                                required
                                                fullWidth
                                                id="email"
                                                label="Email"
                                                name="email"
                                                type="email"
                                                helperText="Note: Please use your active email for code verification"
                                            />
                                            <TextField
                                                inputRef={passwordRef}
                                                sx={{ m: 1 }}
                                                type={isShowPassword ? "text" : "password"}
                                                onChange={(e) => { handlePassword(e.target.value); }}
                                                value={password}
                                                variant="outlined"
                                                size="large"
                                                margin="normal"
                                                required
                                                fullWidth
                                                name="password"
                                                label="Password"
                                                id="password"
                                                helperText={
                                                    password.length === 0 ?
                                                        "Note: Please use a strong password" :
                                                        <Typography display="flex" flexDirection="row" variant='span'>Password Strength: <Typography variant="span" color={passwordColor} sx={{ ml: 1 }}>{passwordStrength}</Typography></Typography>
                                                }
                                                InputProps={{
                                                    endAdornment: (
                                                        <InputAdornment position="end">
                                                            <IconButton onClick={toggleIsShowPassword} size="small" sx={{ mr: -0.5 }}>
                                                                {isShowPassword ?
                                                                    <MdVisibility size={18} />
                                                                    :
                                                                    <MdVisibilityOff size={18} />
                                                                }
                                                            </IconButton>
                                                        </InputAdornment>
                                                    ),
                                                }}
                                            />
                                            <TextField
                                                inputRef={repasswordRef}
                                                sx={{ m: 1 }}
                                                type={isShowRePassword ? "text" : "password"}
                                                onChange={(e) => { handleRetypePassword(e.target.value); }}
                                                value={repassword}
                                                variant="outlined"
                                                size="large"
                                                margin="normal"
                                                required
                                                fullWidth
                                                name="repassword"
                                                label="Re-enter Password"
                                                id="repassword"
                                                helperText={<Typography variant="span" color="#E45570">{!passwordMatch && repassword !== "" ? "Password doesn't match" : ""}</Typography>}
                                                InputProps={{
                                                    endAdornment: (
                                                        <InputAdornment position="end">
                                                            <IconButton onClick={toggleIsShowRePassword} size="small" sx={{ mr: -0.5 }}>
                                                                {isShowRePassword ?
                                                                    <MdVisibility size={18} />
                                                                    :
                                                                    <MdVisibilityOff size={18} />
                                                                }
                                                            </IconButton>
                                                        </InputAdornment>
                                                    ),
                                                }}
                                            />
                                            <Grid container spacing={1} sx={{ m: 1 }}>
                                                <Grid item xs={12} sx={{ textAlign: 'center' }}>
                                                    <LoadingButton
                                                        sx={{ p: 1.5, mt: 1, width: 300 }}
                                                        size="large"
                                                        type="submit"
                                                        fullWidth
                                                        variant="contained"
                                                        color="primary"
                                                        loading={loading}
                                                    >
                                                        Next
                                                        <FaArrowRight style={{ marginLeft: 5 }} />
                                                    </LoadingButton>
                                                </Grid>
                                            </Grid>
                                        </Box>
                                    </form>
                                }
                                {step === 1 &&
                                    <form onSubmit={(e) => handleCode(e)} autoComplete="off">
                                        <Box sx={{ mx: 'auto', width: '100%', textAlign: 'center' }}>
                                            <Typography sx={{ mt: 3, ml: 1 }}>We send a verification code to your email</Typography>
                                            <TextField
                                                sx={{ m: 1, mt: 2 }}
                                                onChange={(e) => setCode(e.target.value)}
                                                value={code}
                                                variant="outlined"
                                                size="large"
                                                required
                                                fullWidth
                                                id="code"
                                                label="Verification Code"
                                                name="code"
                                                type="code"
                                                helperText="Note: Enter the latest verification code sent to your email"
                                                autoFocus
                                            />

                                            <Grid container spacing={1} sx={{ m: 1 }}>
                                                <Grid item xs={12} sx={{ textAlign: 'center' }}>
                                                    <Box display="flex" flexDirection="row" justifyContent="center">
                                                        <LoadingButton
                                                            sx={{ p: 1.5, mt: 3, mr: 1.5, width: 150 }}
                                                            size="large"
                                                            fullWidth
                                                            variant="contained"
                                                            color="primary"
                                                            loading={loading}
                                                            onClick={() => handleStep(0)}
                                                        >
                                                            <FaArrowLeft style={{ marginRight: 5 }} />
                                                            Back
                                                        </LoadingButton>
                                                        <LoadingButton
                                                            sx={{ p: 1.5, mt: 3, width: 150 }}
                                                            size="large"
                                                            type="submit"
                                                            fullWidth
                                                            variant="contained"
                                                            color="primary"
                                                            loading={loading}
                                                        >
                                                            Next
                                                            <FaArrowRight style={{ marginLeft: 5 }} />
                                                        </LoadingButton>
                                                    </Box>
                                                </Grid>
                                            </Grid>
                                        </Box>
                                    </form>
                                }
                                {step === 2 &&
                                    <form onSubmit={(e) => handleRegister(e)} autoComplete="off">
                                        <Box sx={{ mx: 'auto', width: '100%', mt: 2 }}>
                                            <TextField
                                                sx={{ m: 1 }}
                                                onChange={(e) => setLastName(e.target.value)}
                                                value={lastName}
                                                variant="outlined"
                                                size="large"
                                                required
                                                fullWidth
                                                id="lastName"
                                                label="Last Name"
                                                name="lastName"
                                                autoFocus
                                            />
                                            <TextField
                                                sx={{ m: 1 }}
                                                onChange={(e) => setFirstName(e.target.value)}
                                                value={firstName}
                                                variant="outlined"
                                                size="large"
                                                required
                                                fullWidth
                                                id="firstName"
                                                label="First name"
                                                name="firstName"
                                            />
                                            <TextField
                                                sx={{ m: 1 }}
                                                onChange={(e) => setMiddleName(e.target.value)}
                                                value={middleName}
                                                variant="outlined"
                                                size="large"
                                                fullWidth
                                                id="middleName"
                                                label="Middle name"
                                                name="middleName"
                                                helperText="(Optional)"
                                            />
                                            <TextField
                                                sx={{ m: 1 }}
                                                onChange={(e) => setNameSuffix(e.target.value)}
                                                value={nameSuffix}
                                                variant="outlined"
                                                size="large"
                                                fullWidth
                                                id="nameSuffix"
                                                label="Name Suffix"
                                                placeholder="example Jr., Sr., III"
                                                name="nameSuffix"
                                                helperText="(Optional)"
                                            />
                                            <Box sx={{ m: 1, width: '100%' }}>
                                                <Birthdate birthdate={birthdate} setBirthdate={setBirthdate} size="large" />
                                            </Box>
                                            <Box sx={{ m: 1, mt: 2, width: '100%' }}>
                                                <Gender gender={gender} setGender={setGender} size="large" />
                                            </Box>
                                            <Box sx={{ m: 1, mt: 2, width: '100%' }}>
                                                <CivilStatus setCivilStatus={setCivilStatus} civilStatus={civilStatus} size="large" />
                                            </Box>
                                            <Box sx={{ m: 1, mt: 2, width: '100%' }}>
                                                <TextField
                                                    onChange={(e) => setContactNumber(e.target.value)}
                                                    value={contactNumber}
                                                    variant="outlined"
                                                    size="large"
                                                    required
                                                    fullWidth
                                                    id="contactNumber"
                                                    label="Mobile Number"
                                                    name="contactNumber"
                                                    InputProps={{
                                                        inputComponent: ContactNumber
                                                    }}
                                                />
                                            </Box>
                                            <Box sx={{ m: 1, mt: 2, width: '100%' }}>
                                                <Nationality nationality={nationality} setNationality={setNationality} size="large" />
                                            </Box> 
                                            <Grid container spacing={1} sx={{ m: 1 }}>
                                                <Grid item xs={12}>
                                                    <Box sx={{ backgroundColor: "#FCFCFC", textAlign: 'center', mt: 2 }}>
                                                        By clicking create, you agree to our
                                                        <Link rel="noopener" href="#" sx={{ pl: 0.5, pr: 0.5 }}>Terms</Link> and
                                                        <Link rel="noopener" href="#" sx={{ pl: 0.5, pr: 0.5 }}>Conditions</Link>, and that you have read our
                                                        <Link rel="noopener" href="#" sx={{ pl: 0.5, pr: 0.5 }}>Privacy</Link> and
                                                        <Link rel="noopener" href="#" sx={{ pl: 0.5, pr: 0.5 }}>Cookie</Link>Policy.
                                                    </Box>
                                                </Grid>
                                                <Grid item xs={12} sx={{ textAlign: 'center' }}>
                                                    <Box display="flex" flexDirection="row" justifyContent="center">
                                                        <LoadingButton
                                                            sx={{ p: 1.5, mt: 3, mr: 1.5, width: 150 }}
                                                            size="large"
                                                            fullWidth
                                                            variant="contained"
                                                            color="primary"
                                                            loading={loading}
                                                            onClick={() => handleStep(1)}
                                                        >
                                                            <FaArrowLeft style={{ marginRight: 5 }} />
                                                            Back
                                                        </LoadingButton>
                                                        <LoadingButton
                                                            sx={{ p: 1.5, mt: 3, width: 150 }}
                                                            size="large"
                                                            type="submit"
                                                            fullWidth
                                                            variant="contained"
                                                            color="primary"
                                                            loading={loading}
                                                        >
                                                            Create
                                                            <FaCheck style={{ marginLeft: 5 }} />
                                                        </LoadingButton>
                                                    </Box>
                                                </Grid>
                                            </Grid>
                                        </Box>
                                    </form>
                                }
                            </Grid>
                        </React.Fragment>
                    }
                </Grid>
                {step === 3 &&
                    <Box display="flex" flexDirection="column" justifyContent="center" alignItems="center" sx={{ width: "100%" }} >
                        <FaRegCheckCircle color="#00BFA5" size={100} style={{ marginTop: 25 }} />
                        <Typography color="#00BFA5" variant="h5" sx={{ mt: 2 }}>Registration Complete</Typography>
                        <Typography variant="h4" sx={{ mt: 3 }}>Welcome to Bangko sa Balay Foundation Inc.</Typography>
                        <Typography variant="h6" sx={{ mt: 1, fontWeight: 'normal' }}>Thank you for choosing us.</Typography>
                        <Button variant="contained" color="primary" sx={{ mt: 3 }} onClick={() => navigate('/login')}>Go to Login<FaSignInAlt style={{ marginLeft: 5 }} /> </Button>
                    </Box>
                }
            </Box>
        </div >
    )
}
