import React from 'react';

// Redux
import { useDispatch, useSelector } from 'react-redux';
import { getBranches } from '../../../../redux/async/branchesAsync';

// MUI Components
import TextField from '@mui/material/TextField';
import MenuItem from '@mui/material/MenuItem';

// Page Component
import LinearProgressBar from '../../shared/LinearProgressBar/LinearProgressBar';


const Branch = (props) => {

    const dispatch = useDispatch();
    const branchRedux = useSelector((state) => state.branches.value);
    const [branch, setBranch] = React.useState(props.branch ?? '');

    const branchOptions = branchRedux.branches.map(data => {
        return {
            value: data.id,
            label: data.branch.toUpperCase()
        }
    });

    const handleChange = (value) => {

        setBranch(value);

        if (props.setBranch) {
            props.setBranch(value)
        }

        if (props.setLabel) {
            let label = branchRedux.branches.filter(data => {
                return value === data.id;
            });

            props.setLabel(label[0].branch)
        }
    }

    React.useEffect(() => {
        dispatch(getBranches());
    }, [dispatch])

    return (
        <div>
            {branchRedux.loading && <LinearProgressBar title={branchRedux.status} />}
            {!branchRedux.loading &&
                <TextField
                    select
                    label="Branch"
                    name="branch"
                    size="large"
                    variant="outlined"
                    value={branch}
                    onChange={(e) => handleChange(e.target.value)}
                    fullWidth
                    required
                    disabled={branchRedux.loading}
                    inputProps={{ MenuProps: { disableScrollLock: true } }} 
                >
                    {branchOptions.map((option) => (
                        <MenuItem key={option.value} value={option.value}>
                            {option.label}
                        </MenuItem>
                    ))}
                </TextField>
            }
        </div>
    )
}

export default Branch;